.footer-bar{
  display:flex;
  justify-content:center;
  border-radius:1.6970562748em;
  filter:drop-shadow(4px 0px 6px rgba(0, 0, 0, 0.3));
  -webkit-filter:drop-shadow(4px 0px 6px rgba(0, 0, 0, 0.3));
  -moz-filter:drop-shadow(4px 0px 6px rgba(0, 0, 0, 0.3));
  -ms-filter:drop-shadow(4px 0px 6px rgba(0, 0, 0, 0.3));
  -o-filter:drop-shadow(4px 0px 6px rgba(0, 0, 0, 0.3));
  position:relative
}

@property --k{
  .footer-bar{
    syntax:"<number>";
    initial-value:0;
    inherits:true
  }
}

.footer-bar .both{
  flex-grow:1;
  content:" ";
  background-color:#fff
}

.footer-bar .left{
  border-radius:1.6970562748em 0 0 0
}

.footer-bar .right{
  border-radius:0 1.6970562748em 0 0
}

.footer-bar nav,.footer-bar a{
  display:grid
}

.footer-bar nav{
  box-shadow:4px 0px 6px rgba(0,0,0,.3);
  background:#fff;
  grid-auto-flow:column;
  padding:0 1.5em;
  --mask: linear-gradient(red 0 0), radial-gradient( circle at 0 2.1213203436em, transparent calc(2.1213203436em + -0.5px), gold calc(2.1213203436em + 0.5px) ) calc((var(--k) + 0.5) *7.5em + 1.5em + -3.1819805153em) 0 / 2.1213203436em 0.75em no-repeat, radial-gradient( circle at 100% 2.1213203436em, transparent calc(2.1213203436em + -0.5px), gold calc(2.1213203436em + 0.5px) ) calc((var(--k) + 0.5) *7.5em + 1.5em + 1.0606601718em) 0 / 2.1213203436em 0.75em no-repeat, radial-gradient( circle at calc((var(--k) + 0.5) *7.5em + 1.5em) 0.375em, white calc(0.375em + -1px), transparent 0.375em ), radial-gradient( circle at calc((var(--k) + 0.5) *7.5em + 1.5em) -0.75em, blue calc(2.1213203436em + -0.5px), transparent calc(2.1213203436em + 0.5px) );
  mask:var(--mask);
  -webkit-mask:var(--mask);
  -webkit-mask-composite:xor,source-over,source-over;
  mask-composite:exclude,add,add;
  font:.625em/1.5 ubuntu,sans-serif;
  transition:--k .35s cubic-bezier(0.5, -0.5, 0.5, 1.5)
}

.footer-bar .nav-item{
  --dif: calc(var(--i) - var(--k));
  --abs: max(-1 * var(--dif), var(--dif));
  --not-sel: min(1, var(--abs));
  --sel: calc(1 - var(--not-sel));
  box-sizing:border-box;
  place-content:center;
  padding-top:1.5em;
  width:7.5em;
  height:7.5em;
  color:gray;
  text-align:center;
  text-decoration:none;
  text-transform:capitalize
}

.footer-bar .nav-item .icon{
  font-size:2em;
  color:gray;
  fill:gray;
  -webkit-filter:grayscale(1);
  filter:grayscale(1)
}

.footer-bar .nav-item .label{
  -webkit-filter:sepia(var(--hl));
  filter:sepia(var(--hl))
}

.footer-bar .nav-item.active .label{
  color:#2f80ed;
  -webkit-filter:none;
  filter:none
}

.footer-bar .nav-item.active .icon{
  color:#2f80ed;
  fill:#2f80ed;
  -webkit-filter:none;
  filter:none
}

.footer-bar .nav-item:focus{
  outline:none
}

.footer-bar .nav-item:focus,.footer-bar .nav-item:hover{
  --hl: 1
}

.footer-bar .middle-button-container{
  display:flex;
  justify-content:center;
  align-items:center;
  position:absolute;
  top:-1.875em;
  background-color:#2f80ed;
  width:4.725em;
  height:4.725em;
  border-radius:50%;
  color:#fff;
  fill:#fff;
  box-shadow:4px 0px 6px rgba(0,0,0,.3)
}

.footer-bar .middle-button-container:hover{
  background-color:#2059a5;
  box-shadow:6px 0px 8px rgba(0,0,0,.3)
}

.user-level-icon-small{
  top:45%;
  left:50%;
  color:#fff;
  transform:translate(-50%, -50%);
  position:absolute
}

.user-level-icon-large{
  top:45%;
  left:50%;
  transform:translate(-50%, -50%);
  position:absolute;
  font-size:26px;
  font-weight:800;
  color:#fff
}

